/* -------- GRID -------- */

/*
 * This file define main grid layout
 * and width for each section
 */

#main-container {
  display: table;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#main-container-inner {
  display: table-row;
}

#main-container-inner > section {
  display: table-cell;
  vertical-align: top;
  position: relative;
}

#main-container-inner > section > div {
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding-bottom: 50px;
  -webkit-overflow-scrolling: touch;
}

#main-container-inner > section > div > div,
#main-container-inner > section > div > section {
  position: relative;
}

#main-container-inner #user-panel {
  width: 200px;
}

#main-container-inner #admin-menu {
  width: 88px;
}

#main-container-inner #main-trees {
  width: 271px;
}

#main-container-inner #main-trees #main-trees-container {
  width: 271px;
}

#main-container-inner #main-trees #main-trees-container > div {
  width: 271px;
  height: 100%;
}

#main-container-inner #main-trees.minified {
  width: 1px;
}

#main-container-inner #main-trees.minified #main-trees-overlay {
  opacity: 0.7;
}

#main-container-inner #main-trees.minified #main-trees-container {
  left: -67.75px;
}

#main-container-inner #main-content > div {
  overflow-y: auto;
}

#main-container-inner #main-content #main-content-overlay {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #000;
  opacity: 0;
  z-index: 4;
}

#main-container-inner #main-content #canvasloader-container {
  position: fixed;
  top: 50%;
  left: 50%;
  margin: -20px 0 0 -20px;
  z-index: 12;
}

#main-container-inner #main-content #back-top-button {
  display: none;
  position: fixed;
  right: 15px;
  bottom: 15px;
  width: 36px;
  height: 36px;
  line-height: 36px;
  text-align: center;
  background: #fff;
  border: 1px solid #cbcbcb;
  color: #808080;
  cursor: pointer;
  z-index: 3;
}

#main-container-inner #main-content #back-top-button:hover {
  background: #e6e6e6;
  border-color: #b2b2b2;
}

@media (min-width: 960px) {
  #main-container-inner #main-content #back-top-button {
    display: block;
  }
}

/* -------- Dashboard -------- */

#latest-sources {
  text-align: center;
}

.latest-source {
  display: inline-block;
  max-width: 150px;
  vertical-align: top;
  margin: 10px 15px 20px;
}

.latest-source figure {
  position: relative;
  display: block;
  width: 150px;
  height: 150px;
}

.latest-source figure .no-thumb {
  border-radius: 75px;
  width: 150px;
  height: 150px;
  background-color: #CCC;
  display: block;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: -webkit-transform ease 0.3s;
  transition: -webkit-transform ease 0.3s;
  transition: transform ease 0.3s;
  transition: transform ease 0.3s, -webkit-transform ease 0.3s;
}

.latest-source figure img {
  display: block;
}

.latest-source figure img.source-image {
  border-radius: 75px;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: -webkit-transform ease 0.3s;
  transition: -webkit-transform ease 0.3s;
  transition: transform ease 0.3s;
  transition: transform ease 0.3s, -webkit-transform ease 0.3s;
}

.latest-source figure img.user-image {
  border-radius: 30px;
  position: absolute;
  bottom: 0;
  right: 0;
  border: 5px solid #F8F8F8;
}

.latest-source .source-title,
.latest-source .source-update-date {
  color: #808080;
  font-size: 11px;
  text-decoration: none;
  margin: 0px;
  padding: 5px 0px;
}

.latest-source .source-title {
  padding-top: 15px;
  border-bottom: 1px dashed #CCC;
  font-weight: bold;
}

.latest-source > a:hover {
  text-decoration: none;
}

.latest-source > a:hover .no-thumb,
.latest-source > a:hover .source-image {
  -webkit-transform: scale(0.9,0.9);
          transform: scale(0.9,0.9);
}

.latest-source > a:hover .source-title,
.latest-source > a:hover .source-update-date {
  text-decoration: none;
  color: #333;
}

