}

#admin-menu header {
  height: 140px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #E5E5E5;
}

#admin-menu header hr {
  border-top: 1px dashed #c4c4c4;
  border-bottom: 1px dashed white;
  height: 0px;
  margin: 0px;
}

#admin-menu li {
  display: block;
  position: relative;
  /* width: 30px; */
  margin: 0px auto;
  padding: 11px 10px 10px;
  background: white;
  text-align: center;
}

#admin-menu li:before {
  position: absolute;
  content: "";
  display: block;
  width: 28px;
  height: 1px;
  background-color: #D8D8D8;
  left: 29px;
  top: 0px;
}

#admin-menu li:first-child:before {
  display: none;
}

#admin-menu li.uk-parent p .info {
  min-width: 211px;
}

#admin-menu li.uk-parent .info {
  padding: 0 42px 0 20px;
}

#admin-menu li.uk-parent .info:after {
  position: absolute;
  content: "";
  display: block;
  width: 9px;
  height: 6px;
  right: 15px;
  top: 24px;
  background-image: url(../assets/img/matrix@2x.png);
  background-repeat: no-repeat;
  background-size: 620px 420px;
  background-position: 0 0;
  image-rendering: optimizeSpeed;
  /* Legal fallback                 */
  image-rendering: pixellated;
  /* Firefox                        */
  image-rendering: -o-crisp-edges;
  /* Opera                          */
  image-rendering: -webkit-optimize-contrast;
  /* Chrome (and eventually Safari) */
  image-rendering: optimize-contrast;
  /* CSS3 Proposed                  */
  -ms-interpolation-mode: nearest-neighbor;
  /* IE8+ */
  background-position: -20px -20px;
}

#admin-menu li i {
  font-size: 28px;
}

#admin-menu li .info {
  display: none;
  position: absolute;
  z-index: 999;
  left: 88px;
  background-color: #EBEBEB;
  height: 53px;
